@import url(https://fonts.googleapis.com/css?family=PT+Serif:ital,wght@0,400;0,700;1,400;1,700&display=swa);
*{margin:0;padding:0;box-sizing:border-box;font-family:'PT Serif', serif}* body{display:flex;justify-content:center;align-items:center;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;width:100vw;overflow-x:hidden;background-image:url(/country-api/static/media/color.4b5a50c7.png);background-position:center;background-repeat:repeat;background-size:cover}* body .title{text-align:center;margin-top:3vh}* body form{width:90vw;height:90vh;text-align:center;margin:3vh 0}* body form h1{text-align:center}* body form .container{width:90vw;margin:0 auto;display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-gap:20px;margin:3vh 0}* body form .container .small-box{width:20vw;height:20vh;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center;border:1px solid grey;border-radius:5px;padding:25px;cursor:pointer;-moz-osx-font-smoothing:grayscale;-webkit-backface-visibility:hidden;backface-visibility:hidden;transform:translateZ(0);transition:transform 0.25s ease-out}* body form .container .small-box:hover{transform:scale(1.05);border:1px solid #020202}* body form .container .small-box span{font-size:0.8vw}* body form .container .small-box img{width:5vw;height:5vh}* .loading{font-size:4rem;margin:45vh auto;color:gray}

